  display: block;
  position: relative;
}

.loader:before {
  content: "";
  box-sizing: border-box;
  border: 5px solid #aaa7a7;
  border-radius: 50%;
  animation: prixClipFix 2s linear infinite;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

@keyframes rotate {
  100% {
    transform: rotate(360deg);
  }
}

